/* #hero
============================================ */

.section--hero{
    padding: 0;

    .image{
        position: relative;
        height: calc(100vh - 250px);
        min-height: 700px;
        margin-bottom: 0;
        transition: $transition;

        &:before{
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            padding-top: calc(750 / 1200 * 100%);
        }

        &:after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 10;
            background: rgb(0,0,0);
            background: linear-gradient(90deg, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
        }
    }

    .text{
        position: relative;
        z-index: 20;

        h1, h2, h3, h4, a{
            color: #fff;
            text-decoration: none;
        }

        h2,h3,h4{
            text-transform: none;
        }
    }
}

